)}.categorie-img3{width:100px;height:100px;background-image:url("../public/icones/idee.png");background-repeat:no-repeat;background-size:90%;background-position:center;transition:transform .5s;filter:drop-shadow(3px 5px 2px rgba(0, 0, 0, 0.5))}.categorie-img3:hover{transform:scale(3);filter:drop-shadow(3px 5px 5px rgba(0, 0, 0, 0.5))}.categorie-img4{width:100px;height:100px;background-image:url("../public/icones/puzzle.png");background-repeat:no-repeat;background-size:90%;background-position-x:center;transition:transform .5s;filter:drop-shadow(3px 5px 2px rgba(0, 0, 0, 0.5))}.categorie-img4:hover{transform:scale(3);filter:drop-shadow(3px 5px 5px rgba(0, 0, 0, 0.5))}.categorie-img5{width:100px;height:100px;background-image:url("../public/icones/signet.png");background-repeat:no-repeat;background-size:85%;background-position:center;transition:transform .5s;filter:drop-shadow(3px 5px 2px rgba(0, 0, 0, 0.5))}.categorie-img5:hover{transform:scale(3);filter:drop-shadow(3px 5px 5px rgba(0, 0, 0, 0.5))}.actu{padding:30px;display:flex;flex-wrap:wrap;flex-direction:row;justify-content:space-around;background-color:#00bdb6}.actu-title{margin-top:0;font-size:2.5rem;color:#ffebcd;text-shadow:rgba(0,0,0,.3) 5px 3px 5px;order:-1;flex:1 0 100%}.actu-post{margin:20px;--clay-background: blanchedalmond;--clay-shadow-inset-primary: -8px -8px 16px 0 rgba(0, 0, 0, 0.15);color:#000;align-items:baseline}.actu-img{width:300px;padding:30px;filter:drop-shadow(3px 5px 2px rgba(0, 0, 0, 0.5))}.contact-container{padding:30px;display:flex;flex-wrap:wrap;flex-direction:row;justify-content:space-evenly;align-items:center}.contact-container-coordonnées{text-align:left}.contact-container-iframe{width:500px;height:300px;padding:30px;border-style:hidden;border-radius:50px;filter:drop-shadow(3px 5px 2px rgba(0, 0, 0, 0.5))}